/** @jsx jsx */
import React from "react"
import { Link } from "gatsby"
import { jsx, useThemeUI } from "theme-ui"
//import { useSiteMetadata } from "StaticQueries/site-metadata"
import ColorModeSwitcher from "Components/modules/colorModeSwitcher"
//import QiwiUrl from "Components/payments/qiwi"
import SearchBar from "Components/modules/searchBar"

export default function IndexPage() {
  const context = useThemeUI()
  const { theme } = context

  return (
    <>
      <aside
        sx={{
          display: "flex",
          flexDirection: "column",
          width: "320px",
          height: "100%",
          backgroundColor: "background_secondary",
          boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque}`,
        }}
      >
        {/* Aside header */}
        <div
          sx={{
            display: "flex",
            flexDirection: "column",
            width: "100%",
            backgroundColor: "background_primary",
            boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque},inset 0px -1px 0px ${theme.colors.separator_opaque}`,
          }}
        >
          <Link
            to="/"
            sx={{
              variant: ["element.size.large"],
              textAlign: "center",
              fontWeight: "bold",
              color: "text",
              ":hover": {
                color: "text",
                textDecoration: "none",
              },
            }}
          >
            Site name
          </Link>

          {/* Search */}
          <div
            sx={{
              px: "16px",
              py: "8px",
              display: "flex",
              flexDirection: "column",
            }}
          >
            <SearchBar placeholder="Поиск по товарам" elementSize="medium" />
          </div>
        </div>
      </aside>
      <ColorModeSwitcher
        styles={{
          variant: [
            "element.size.medium",
            "element.type.button",
            "button.primary",
          ],
          position: "absolute",
          top: "0px",
          right: "0px",
        }}
        text="Switch color mode"
      />
    </>
  )
}
